import './record-button.styles.scss';


const RecordButton = ({ startVideo }) => {
  const toggleAnimation = (e) => {
    const recordBtn = document.getElementById('play');
    const startIcon = document.querySelector('.start-icon');
    const stopIcon = document.querySelector('.stop-icon');

    if (recordBtn.hasAttribute('class')) {
      recordBtn.removeAttribute('class');
      stopIcon.style.display = 'none';
      startIcon.style.display = 'initial';
      startVideo(false);
    } else {
      recordBtn.classList.add('animate');
      startIcon.style.display = 'none';
      stopIcon.style.display = 'initial';
      startVideo(true);
    }

  }

  return (
    <div className='button-container'>
      <svg onClick={toggleAnimation} id="play" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" height="100px" width="100px"
        viewBox="0 0 100 100" enableBackground="new 0 0 100 100">
        <path className="stroke-solid" fill="none" stroke="white" d="M49.9,2.5C23.6,2.8,2.1,24.4,2.5,50.4C2.9,76.5,24.7,98,50.3,97.5c26.4-0.6,47.4-21.8,47.2-47.7
    C97.3,23.7,75.7,2.3,49.9,2.5"/>
        <path className="stroke-dotted" fill="none" stroke="white" d="M49.9,2.5C23.6,2.8,2.1,24.4,2.5,50.4C2.9,76.5,24.7,98,50.3,97.5c26.4-0.6,47.4-21.8,47.2-47.7
    C97.3,23.7,75.7,2.3,49.9,2.5"/>
        <path className="start-icon" fill="white" d="M38,69c-1,0.5-1.8,0-1.8-1.1V32.1c0-1.1,0.8-1.6,1.8-1.1l34,18c1,0.5,1,1.4,0,1.9L38,69z" />
        <path
          className='stop-icon'
          fill='white'
          d="M 11 10 L 17 10 L 17 26 L 11 26 M 20 10 L 26 10 L 26 26 L 20 26"
        />
      </svg>
    </div>
  )

}

export default RecordButton;